












































































































import Vue from 'vue';
import ReceiveCard from '@/components/receive/ReceiveCard.vue';
import ReceiveConfirmation from '@/components/receive/ReceiveConfirmation.vue';
import ReceiveSummary from '@/components/receive/ReceiveSummary.vue';
import AppreciationCard from '../data/ac';
import { Route } from 'vue-router';

export interface ReceiveOptions {
  request: boolean;
  known: boolean;
  ac: AppreciationCard;
  found: boolean;
}

export default Vue.extend({
  name: 'Receive',
  components: {
    ReceiveCard,
    ReceiveConfirmation,
    ReceiveSummary
  },
  data() {
    return {
      step: 0 as number,
      receiveOptions: {} as ReceiveOptions,
      loading: false as boolean,
      sheet: false as boolean,
      nextDisabled: false as boolean
    }
  },
  methods: {
    updateAC(options: ReceiveOptions): void {
      this.receiveOptions = options;
      this.buttonShown();
      this.step = 2;
    },
    buttonShown(): void {
      this.nextDisabled = true;
      setTimeout(() => this.nextDisabled = false, 8000);
    },
    changeView(i: number): void {
      if (this.step === 2 && i === -1) this.sheet = true;
      else if (this.step === 1 && i === -1) this.$router.back();
      else this.step += i;
    },
    cancelSend(): void {
      this.sheet = false;
      this.step = 1;
    },
  },
  computed: {
    stepTitle(): string {
      switch (this.step) {
        case 1: return 'Scan QR code'
        case 2: return 'Let other person scan'
        default: return 'Transaction summary'
      }
    },
    progressValue(): number {
      return (this.step - 1) * (100 / 2);
    },
  },
  beforeRouteLeave(to: Route, from: Route, next: CallableFunction) {
    if (this.step < 2) next();
    else if (to.path === "/") {
      if (this.step === 2) this.sheet = true;
      else this.step--;
      next(false);
    } else {
      next();
    }
  }
});
