















































import Vue from 'vue';
import AppreciationCard from '@/data/ac';
import { TOKEN } from '../../interfaces/iac';
import iconConfig from '@/store/misc/icons';

export default Vue.extend({
  name: 'ReceiveSummary',
  data() {
    return {
      iconConfig
    }
  },
  props: {
    ac: Object as () => AppreciationCard
  },
  computed: {
    senderName(): string {
      return this.ac && this.ac.sender
        ? this.ac.sender.name
        : "";
    },
    tokenReceived(): boolean {
      return this.ac ? this.ac.token === TOKEN.AWESOME : false;
    }
  }
});
