









































































































































import Vue from 'vue'
import { minimizedToPersonData, PersonData, MinimizedPerson } from '@/interfaces/ipeople';
import { minimizedToACData, MinimizedAppreciationCard, AppreciationCardData, TOKEN } from '@/interfaces/iac';
import AppreciationCard from '../../data/ac';

import { peopleModule } from '@/store/modules/people';
import { acModule } from '@/store/modules/acs';
import QRReader from '@/components/QRReader';
import PersonTile from '@/components/PersonTile.vue';
import Person from '@/data/person';
import { ReceiveOptions } from '@/views/Receive.vue';
import iconConfig from '@/store/misc/icons';

export default Vue.extend({
  name: 'ReceiveCard',
  components: {
    PersonTile
  },
  data: function() {
    return {
      qrReader: {} as QRReader,
      resultText: "" as string,
      loading: false as boolean,
      extraDetails: false as boolean,
      alreadyKnown: false as boolean,
      addedPerson: false as boolean,
      showError: false as boolean,
      errorMessage: "" as string,
      showUpdatedContact: false as boolean,
      showNewContact: false as boolean,
      localSender: undefined as Person | undefined,
      personData: undefined as PersonData | undefined,
      incomingMAC: {} as MinimizedAppreciationCard,
      iconConfig
    }
  },
  props: {
    active: Boolean,
    updatecb: Function
  },
  methods: {
    handleError(message: string): void {
      this.errorMessage = message;
      this.showError = true; 
    },
    handleNewContact(): void {
      this.showNewContact = true;
    },
    handleUpdatedContact(): void {
      this.showUpdatedContact = true;
    },
    parseResult(qrData: string) {
      if (this.loading) 
        return;

      this.loading = true;

      let result: any;
      try {
        result = JSON.parse(qrData);
      }
      catch(err) {
        return this.handleError("Invalid data structure in QR Code.");
      }

      if (!this.checkACValidity(result)) 
        return this.handleError("That is not a valid Appreciation Card.");
        
      this.localSender = peopleModule.personByEmail(result.e);
        
      if (result.p) {
        // Requesting contact details
        this.extraDetails = true;
        this.alreadyKnown = (this.localSender !== undefined && this.localSender.isNewContact) ? true : false;
        if (!this.checkPersonValidity(result.p)) 
          return this.handleError("Invalid contact details found in package.");
          
        const mp: MinimizedPerson = result.p;
        mp.e = result.e;
        this.personData = minimizedToPersonData(mp);
        this.incomingMAC = result as MinimizedAppreciationCard;

        if (!this.localSender) 
          this.handleNewContact();
        else {
          if (this.localSender.isEqual(this.personData))
            this.addAC(result);
          else {
            
            this.handleUpdatedContact();
          }
        }
      } 
      else {
        // No request for additional contact details
        if (!this.localSender) 
          return this.handleError("This person is not in your contacts, please ask him to upgrade the details.");
          
        this.addAC(result);
      }
    },
    dismissDialog() {
      this.loading = false;
      this.showError = false;
      this.showUpdatedContact = false;
      this.showNewContact = false;
      this.qrReader.start();
    },
    addNewContact() {
      this.showNewContact = false;
      //TODO: handle undefined
      if (!this.personData) return;
      peopleModule.addPerson(this.personData)
        .then(() => this.addAC(this.incomingMAC));
    },
    updatedContact() {
      this.showUpdatedContact = false;
      //TODO: handle undefined
      if (!this.personData) return;
      peopleModule.updatePerson(this.personData)
        .then(() => this.addAC(this.incomingMAC));
    },
    noUpdatedContact() {
      this.showUpdatedContact = false;
      this.addAC(this.incomingMAC);
    },
    addAC(mac: MinimizedAppreciationCard): void {
      const foundAC = acModule.acByMinimizedAC(mac);
      if (foundAC) {
        console.log('duplicate');
        this.loading = false;
          this.qrReader.stop();
          this.updatecb({
            request: this.extraDetails,
            known: this.alreadyKnown,
            ac: foundAC,
            found: true
          } as ReceiveOptions);
      } else {
        const acd: AppreciationCardData = minimizedToACData(mac);
        if (acModule.canObtainAwesomeToken(acd)) acd.token = TOKEN.AWESOME;
        acModule.addAC(acd)
        .then((ac: AppreciationCard) => {
          this.loading = false;
          this.qrReader.stop();
          this.updatecb({
            request: this.extraDetails,
            known: this.alreadyKnown,
            ac: ac,
            found: false
          });
        });
      }
    },
    checkACValidity(jsonResult: any): boolean {
      return (jsonResult.e
        && jsonResult.m
        && jsonResult.d
        && jsonResult.t
        && jsonResult.s
      ) ? true : false;
    },
    checkPersonValidity(jsonResult: any): boolean {
      return (jsonResult.n) ? true : false;
    }
  },
  computed: {
    incomingPerson(): Person | undefined {
      return this.personData ? new Person(this.personData) : undefined;
    }
  },
  watch: {
    active(newActive: boolean): void {
      if (newActive) 
        this.qrReader.start();
      else
        this.qrReader.stop();
    }
  },
  mounted: function() {
    this.qrReader = new QRReader(this.$refs.canvas as HTMLCanvasElement);
    this.qrReader.on('qrReceived', this.parseResult);
    this.qrReader.start();
  },
  beforeDestroy: function() {
    this.qrReader.stop();
  }
})
