











































import Vue from 'vue';
import QRGenerator, { ERROR_CORRECTION } from '@/components/QRGenerator.vue';
import AppreciationCard from '@/data/ac';
import { peopleModule } from '../../store/modules/people';
import { ReceiveOptions } from '../../views/Receive.vue';
import { TOKEN } from '../../interfaces/iac';
import iconConfig from '@/store/misc/icons';

export default Vue.extend({
  name: 'ReceiveConfirmation',
  components: {
    QRGenerator
  },
  props: {
    options: Object as () => ReceiveOptions
  },
  data: function() {
    return {
      confirmMsg: "",
      currentCorrection: ERROR_CORRECTION.MEDIUM as ERROR_CORRECTION,
      errorCorrection: ERROR_CORRECTION,
      iconConfig
    }
  },
  methods: {
    generateMessage() {
      this.confirmMsg = this.options.ac
      ? this.options.request
          ? JSON.stringify(this.options.ac.confirm(true))
          : JSON.stringify(this.options.ac.confirm(false))
      : "";
    }
  },
  computed: {
    name(): string {
      return this.options ? this.options.ac.sender.name : "";
    },
    email(): string {
      return this.options ? this.options.ac.sender.email : "";
    },
    newContact(): boolean {
      return this.options ? this.options.request && !this.options.known : false;
    },
    updateRequested(): boolean {
      return this.options ? this.options.request && this.options.known : false;
    }
  },
  watch: {
    ac() {
      this.generateMessage();
    }
  },
  mounted: function() {
    this.generateMessage();
  }
});
